*{
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    background-color: #ff7675;
}
h2{
    padding: 18px;
    color: #fff;
    background-color: #ff7675;
}

h3{
    text-align: center;
    font-weight: bold;
    color: #d63031;
}

.app{
    padding: 30px;
    background-color: #fff;
    width: 80%;
    margin: 50px auto;
    box-shadow: 1px 2px 5px #eee;
}

input{
    width: 80%;
    border: 0;
    padding: 6px 10px;
    background-color: #f5ded8;
}

button{
    width: 20%;
    border: 0;
    margin: 0;
    outline: 0;
    padding: 6px 0;
    color: #fff;
    font-weight: bold;
    color: #636e72;
    cursor: pointer;
    background-color: #55efc4;
}

ul{
    list-style: none;
    padding: 0;
}

ul > form{
    margin: 15px 0;
}
li{
    margin: 15px 0;
}

li > span{
    display: inline-block;
    width: 60%;
    color: #2d3436;
}

li > button:first-of-type{
    color: #fff;
    background-color: #636e72;
    margin-right: 1%;
    width: 19%;
}


li > button:last-of-type{
    color: #fff;
    background-color: #d63031;
    
}







